#study{
    background-color: #122535;
    color: #F5F0E1;
    padding-top: 5%;
    padding-bottom: 5%;    
}

#picStudy{
    position: absolute;
    width: 15%;
    left: 10%;
    cursor: pointer;
    margin-top: 2%;
    transition: 1s;
    &:hover{
        width: 17%;
    }
   
}


#carouselStudy{
    margin-top: 2%;
    width: 80%;
    left: 10%;
    transition: 0.8s;
}

@media only screen and (max-width: 768px) {
    #carouselStudy {
        width: 100%;
        margin-top: 2%;
        transition: 0.8s;
        left: 0%;
        visibility: visible;
    }

    #picStudy{
        position: absolute;
        width: 25%;
        transform: translateX(-50%);
        cursor: pointer;
        margin-top: 25%;
        transition: 1s;
    }
}