* {
    box-sizing: border-box;
}
html, body{
    padding: 0;
    margin: 0px;
    font-family: 'Maven Pro', sans-serif;
    color: #436D5D;
    
}


#video{
    background-color: #122535;
    position: fixed;
    z-index: -1;
    max-width: 100%;
	height: auto;
} 

.background {
    background-image: url('../images/background.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    filter: blur(3px) contrast(95%);

}

.styleHeader{
    filter: blur(0px) contrast(100%);
    background: rgba(18, 37, 53, 0.55);
    color: #F5F0E1;
    padding: 2rem 3rem;
    margin-left: 29%;
    margin-right: 29%;
    position: absolute;
    top: 27%;
    left: 21%;
    transform: translateX(-50%);
    transition: 1s;
    &:hover{
        padding: 2.5rem 3rem;

    }
}
.styleHeader p{
    font-size: 1.2rem;
}


.italic {
    font-style: italic;
}

.w-{
    &7{
        width: 7rem;
    }
    &10{
        width: 10rem;
    }
    &12{
        width: 12rem;
    }
    &17{
        width: 17rem;
    }
    &20{
        width: 20rem;
    }
}

.borderTitle {
    border-bottom: solid #F5F0E1;
    border-radius: 30px;
    margin-bottom: 2%;
    width: 20%;
    margin-left: 40%;
    
}

.footer{
    background-color: #122535;
    color: #F5F0E1;
    padding-top: 3%;
    padding-bottom: 2%;
    border-top: solid #F5F0E1;
    padding-right: 25%;
    padding-left: 25%;
}

.picShifumi{
    position: absolute;
    top: 25%;
    left: 10%;
    width: 15%;
    transition: 1s;
    cursor: pointer;
    &:hover{
        width: 16%;
    }
}