$breakpoints: (
    mobile: 599px
);

  .shifumi-card{
    background-color: #0f1922;
    color: #F5F0E1;
    width: 40%;
    position: absolute;
    transform: translateX(-50%);
    top: 20%;
    transition: 1s;
    height: 300px;
    z-index: 1;
    border-radius: 10px 100px / 120px;
    box-shadow: 5px 5px 5px #F5F0E1;
    h3{
        color: #F5F0E1;
    }
    @media screen and (max-width: map-get($breakpoints, mobile)) {
        width: 20rem;
       
    }
}

.shifumi-signe{
    font-size: 2rem;
    color : #FA4601;
    &-item{
        cursor: pointer;
        transition: 0.5s;
        &:hover{
            font-size: 2.2rem;
            color: #ff8758;
        }
    }

}

#resultat{
    font-size: 1rem;
    color: #F5F0E1;

}