#experience{
    background-color: #122535;
    color: #F5F0E1;
    padding-top: 5%;
    padding-bottom: 5%;
    border-top: solid #F5F0E1;
}

.styleRow{
    padding: 1rem;
}

.cardStyle{
    margin: 0.5rem;
    color: #122535;
    
}

.exempleExperience{
    padding-top: 1rem;
}

.boutonExperience{
    text-decoration:none; 
    cursor: pointer;
    margin-top: 1rem;
    background-color: #FA4601;
    color: #F5F0E1;
    display: inline-block;
    font-weight: 400;
    line-height: 1.5;
    border: 1px solid transparent;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    border-radius: 0.25rem;
    transition: color 0.15s ease-in-out, background-color 0.5s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    &:hover{
        background-color: white;
        color: #122535;
        border: 1px solid #122535;
    }
}

.experienceBody{
    background-color: #F5F0E1;
    border-radius: 1rem;
    width: 80%;
    margin-left: 10%;
    color: #122535;
}

.menuExperience{
    display: flex;
    list-style-type: none;
    flex-direction: column;
    justify-content: center;
    
}

.linkExperience{
    background: none;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
    margin: 2rem 1rem 2rem 2rem;
    font-size: 16px;
    &:hover{
        text-decoration: underline;
    }
    &-active{
        font-weight: bold;
        text-decoration: underline;
    }
}

.iconeImage{
    font-size: 4rem;
    width: auto;
    cursor: pointer;
    transition: 0.25s;
    &:hover{
        font-size: 4.5rem;
        color: #FA4601;
    }
}

.experienceImage{
    position: fixed;
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1200;
    
}

.carousel-item {
    padding: 4rem;
    background: rgba(18, 37, 53, 0.85);
}

.hidden{
    transition: 0.5s;
    visibility: hidden;
}

#picExperience{
    position: absolute;
    width: 10%;
    right: 2%;
}