#contact{
    background-color: #122535;
    color: #F5F0E1;
    padding-top: 5%;
    padding-bottom: 5%;
    border-top: solid #F5F0E1;
    padding-right: 25%;
    padding-left: 25%;

}

#mail, #git, #linkedin{
    font-size: 2rem;
    transition: 0.5s;
    cursor: pointer;
    &:hover{
        color: #FA4601;
    }
}

.picContact{
    position: absolute;
    left: 5%;
    width: 15%;
}