.navbar{
    display: flex;
    justify-content: center;
    background-color: transparent;
    transition: 1s;
    &-link{
        font-size: 1.2rem;
        transition: 1s;
        color: #F5F0E1 !important;
        &:hover{
            color: #FA4601 !important;
        }
    }
}
.nav-link.active{
    color: #FA4601 !important;
}